/* stylelint-disable at-rule-no-unknown */
table.debugprint {
  @extend .table;
  @extend .table-light;
  @extend .table-striped;
  @extend .table-bordered;
  @extend .table-sm;
  @extend .table-hover;

  .true {
    color: map-get($theme-colors, 'success');
  }

  .false {
    color: map-get($theme-colors, 'danger');
  }
}
